/** @jsx jsx */
import { jsx, Styled } from "theme-ui";

import { graphql, Link } from "gatsby";
import { mapCraft } from "./mappings";

import { useEffect, useState } from "react";
import InstantSearch from "@components/InstantSearch";

const ArticleTemplate = (props) => {
  const { category } = mapCraft(props.data.craft);

  const { title, description } = category;

  useEffect(() => {
    if (title) {
      setRefinements({
        ...defaultRefinements,
        collection: [title],
      });
    }
  }, []);

  const [defaultRefinements, setRefinements] = useState({
    type: [],
    collection: [],
  });

  return (
    <div>
      <InstantSearch
        refinementType="collections"
        defaultRefinement={defaultRefinements.collection}
        isSearch={false}
        heading={title}
        description={description}
      />
    </div>
  );
};

export default ArticleTemplate;

export const query = graphql`
  query CollectionsQuery($id: Craft_QueryArgument) {
    craft {
      category(id: [$id]) {
        ... on Craft_collections_Category {
          title
          description
        }
      }
    }
  }
`;
